/*======= Home =======*/

.home {
  position: relative;
  background-color: #ededed;
  height: 100%;
  min-height: 600px;
  position: relative;
  display: block;
  width: 100%;
}

.home-wrapper {
  color: #374760;
  padding: 200px 0px 150px 0px;
}

.home-wrapper-sm {
  padding: 160px 0px 115px 0px;
}

.home-wrapper p {
  line-height: 24px;
  margin: 30px auto 40px auto;
  font-size: 16px;
  width: 85%;
}

/* Subscribe Form */

.subscribe-form {
  margin-top: 20px;
}

.subscribe-form form {
  position: relative;
  max-width: 600px;
  margin: 0px auto;
}

.subscribe-form input {
  padding: 15px 20px;
  width: 100%;
  font-size: 17px;
  border: none;
  outline: none !important;
  padding-right: 150px;
  padding-left: 30px;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 30px;
}

.subscribe-form button {
  position: absolute;
  top: 4px;
  right: 4px;
  outline: none !important;
  border-radius: 30px;
  border: none;
  color: #fff;
  font-size: 17px;
  background: #374760;
  padding: 11px 30px;
  cursor: pointer;
}
